import { firebaseConnection } from "../../firebase";
var dataSnapshot = firebaseConnection.database().ref('Table1/Panel');

export const actionTogglePanel = (Panel) => {
    return (dispatch, getState) => {
        if (getState().auth.editMode || getState().auth.addMode) {
            dispatch({ type: 'OFF_ALL_STATE' })
            dispatch({ type: 'TOGGLE_EDIT_PANEL' })
            dispatch({ type: 'TOGGLE_PANEL', Panel })
        }
    }
}
export const actionAddPanel = () => {
    return (dispatch, getState) => {
        const panel = getState().panel.addPanel
        let tempPanel = {
            parent: getState().element.parentScada,
            width: parseInt(panel.width),
            height: parseInt(panel.height),
            left: parseInt(panel.left),
            top: parseInt(panel.top),
            margin: parseInt(panel.margin),
            backgroundColor: panel.backgroundColor,
            padding: parseInt(panel.padding),
            borderRadius: parseInt(panel.borderRadius),
            boxShadow1: parseInt(panel.boxShadow1),
            boxShadow2: parseInt(panel.boxShadow2),
            boxShadow3: parseInt(panel.boxShadow3),
            boxShadowColor: panel.boxShadowColor,
            zIndex: parseInt(panel.zIndex),
        }
        dataSnapshot.push(tempPanel);
        let message = 'Success';
        let status = 'success';
        dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
    }
}
export const actionUpdatePanel = () => {
    return (dispatch, getState) => {
        const editPanel = getState().panel.editPanel;
        if (editPanel.id !== '') {
            dataSnapshot.child(editPanel.id).child('width').set(editPanel.width);
            dataSnapshot.child(editPanel.id).child('height').set(editPanel.height);
            dataSnapshot.child(editPanel.id).child('left').set(editPanel.left);
            dataSnapshot.child(editPanel.id).child('top').set(editPanel.top);
            dataSnapshot.child(editPanel.id).child('margin').set(editPanel.margin);
            dataSnapshot.child(editPanel.id).child('backgroundColor').set(editPanel.backgroundColor);
            dataSnapshot.child(editPanel.id).child('padding').set(editPanel.padding);
            dataSnapshot.child(editPanel.id).child('borderRadius').set(editPanel.borderRadius);
            dataSnapshot.child(editPanel.id).child('boxShadow1').set(editPanel.boxShadow1);
            dataSnapshot.child(editPanel.id).child('boxShadow2').set(editPanel.boxShadow2);
            dataSnapshot.child(editPanel.id).child('boxShadow3').set(editPanel.boxShadow3);
            dataSnapshot.child(editPanel.id).child('boxShadowColor').set(editPanel.boxShadowColor);
            dataSnapshot.child(editPanel.id).child('zIndex').set(editPanel.zIndex);
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
export const actionDeletePanel = () => {
    return (dispatch, getState) => {
        const id = getState().panel.editPanel.id
        if (id !== '') {
            dataSnapshot.child(id).remove();
            dispatch({ type: 'RESET_PANEL_EDIT_FORM' })
            let message = 'Success';
            let status = 'success';
            dispatch({ type: 'SNACK_TOGGLE_ON', message, status });
        }
    }
}
