import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import * as action from '../../../redux/actions/panelAction'
class PanelFormEdit extends Component {
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionPanelEditWidthChange(e.target.value);
  };
  handleChangeHeight = (e) => {
    e.preventDefault();
    this.props.actionPanelEditHeightChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionPanelEditLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionPanelEditTopChange(e.target.value);
  };
  handleChangeMargin = (e) => {
    e.preventDefault();
    this.props.actionPanelEditMarginChange(e.target.value);
  };
  handleChangePadding = (e) => {
    e.preventDefault();
    this.props.actionPanelEditPaddingChange(e.target.value);
  };
  handleChangeBackgroundColor = (e) => {
    e.preventDefault();
    this.props.actionPanelEditBackgroundColorChange(e.target.value);
  };
  handleChangeBorderRadius = (e) => {
    e.preventDefault();
    this.props.actionPanelEditBorderRadiusChange(e.target.value);
  };
  handleChangeBoxShadow1 = (e) => {
    e.preventDefault();
    this.props.actionPanelEditBoxShadow1Change(e.target.value);
  };
  handleChangeBoxShadow2 = (e) => {
    e.preventDefault();
    this.props.actionPanelEditBoxShadow2Change(e.target.value);
  };
  handleChangeBoxShadow3 = (e) => {
    e.preventDefault();
    this.props.actionPanelEditBoxShadow3Change(e.target.value);
  };
  handleChangeBoxShadowColor = (e) => {
    e.preventDefault();
    this.props.actionPanelEditBoxShadowColorChange(e.target.value);
  };
  handleChangeZIndex = (e) => {
    e.preventDefault();
    this.props.actionPanelEditZIndexChange(e.target.value);
  };

  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionUpdatePanel();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift=0,offsetCtrl=0,offestDefault=0;
    if (event.shiftKey ) {//shift
      offsetShift=10;offestDefault=0;
    }else {offsetShift=0;}
    if (event.ctrlKey) {//control
      offsetCtrl=5;offestDefault=0;
    }else{offsetCtrl=0;}
    if (!event.shiftKey && !event.ctrlKey){offestDefault=1}
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionEditMoveLeftDecrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionEditMoveTopDecrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionEditMoveLeftIncrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionEditMoveTopIncrease(offsetShift+offsetCtrl+offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionEditWidthDecrease()
    }
    if (event.keyCode === 38 && (event.shiftKey && event.ctrlKey)) {//Len
      this.props.actionEditHeightDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionEditWidthIncrease()
    }
    if (event.keyCode === 40 && (event.shiftKey && event.ctrlKey)) {//Xuong
      this.props.actionEditHeightIncrease()
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress} style={{ height: '100vh', overflow: 'auto' }}>
          <h3 className="text-center text-info">EDIT PANEL</h3>

          <button type="button" onClick=
            {this.props.actionUpdatePanel}
            className="btn btn-primary btn-block">Update</button>

          <div className="dropdown-divider"></div>

          <button type="button" onClick={this.props.actionDeletePanel}
            className="btn btn-primary btn-block">DELETE
                </button>
          <div className="form-group">
            <small className="form-text text-muted">ID:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="ID *" id="ID" className="form-control" value={this.props.id} />

            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.left} />

            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.top} />

            <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} placeholder="Width *" id="width" className="form-control" value={this.props.width} />

            <small className="form-text text-muted">Height:</small>
            <input type="text" onChange={this.handleChangeHeight} placeholder="Height *" id="height" className="form-control" value={this.props.height} />

            <small className="form-text text-muted">Background Color:</small>
            <input type="color" style={{width:'100%'}} onChange={this.handleChangeBackgroundColor} value={this.props.backgroundColor} />
            <input type="text" onChange={this.handleChangeBackgroundColor} placeholder="Background Color *" className="form-control" value={this.props.backgroundColor} />

            <small className="form-text text-muted">Border Radius:</small>
            <input type="text" onChange={this.handleChangeBorderRadius} placeholder="Border Radius *" className="form-control" value={this.props.borderRadius} />

            <small className="form-text text-muted">Box Shadow 1:</small>
            <input type="text" onChange={this.handleChangeBoxShadow1} placeholder="BoxShadow1 *" className="form-control" value={this.props.boxShadow1} />

            <small className="form-text text-muted">Box Shadow 2:</small>
            <input type="text" onChange={this.handleChangeBoxShadow2} placeholder="BoxShadow2 *" className="form-control" value={this.props.boxShadow2} />

            <small className="form-text text-muted">Box Shadow 3:</small>
            <input type="text" onChange={this.handleChangeBoxShadow3} placeholder="BoxShadow3 *" className="form-control" value={this.props.boxShadow3} />

            <small className="form-text text-muted">Box Shadow Color:</small>
            <input type="color" style={{width:'100%'}} onChange={this.handleChangeBoxShadowColor} value={this.props.boxShadowColor} />
            <input type="text" onChange={this.handleChangeBoxShadowColor} placeholder="BoxShadowColor *" className="form-control" value={this.props.boxShadowColor} />
            
            <small className="form-text text-muted">Margin:</small>
            <input type="text" onChange={this.handleChangeMargin} placeholder="Margin *" className="form-control" value={this.props.margin} />

            <small className="form-text text-muted">Padding:</small>
            <input type="text" onChange={this.handleChangePadding} placeholder="Padding *" className="form-control" value={this.props.padding} />
          </div>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    id: state.panel.editPanel.id,
    width: state.panel.editPanel.width,
    height: state.panel.editPanel.height,
    left: state.panel.editPanel.left,
    top: state.panel.editPanel.top,
    margin: state.panel.editPanel.margin,
    backgroundColor: state.panel.editPanel.backgroundColor,
    padding: state.panel.editPanel.padding,
    borderRadius: state.panel.editPanel.borderRadius,
    boxShadow1: state.panel.editPanel.boxShadow1,
    boxShadow2: state.panel.editPanel.boxShadow2,
    boxShadow3: state.panel.editPanel.boxShadow3,
    boxShadowColor: state.panel.editPanel.boxShadowColor,
    zIndex: state.panel.editPanel.zIndex,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionUpdatePanel: () => {
      dispatch(action.actionUpdatePanel())
    },
    actionDeletePanel: () => {
      dispatch(action.actionDeletePanel())
    },
    actionPanelEditLeftChange: (item) => {
      dispatch(action.actionPanelEditLeftChange(item))
    },
    actionPanelEditTopChange: (item) => {
      dispatch(action.actionPanelEditTopChange(item))
    },
    actionPanelEditWidthChange: (item) => {
      dispatch(action.actionPanelEditWidthChange(item))
    },
    actionPanelEditHeightChange: (item) => {
      dispatch(action.actionPanelEditHeightChange(item))
    },
    actionPanelEditMarginChange: (item) => {
      dispatch(action.actionPanelEditMarginChange(item))
    },
    actionPanelEditBackgroundColorChange: (item) => {
      dispatch(action.actionPanelEditBackgroundColorChange(item))
    },
    actionPanelEditPaddingChange: (item) => {
      dispatch(action.actionPanelEditPaddingChange(item))
    },
    actionPanelEditBorderRadiusChange: (item) => {
      dispatch(action.actionPanelEditBorderRadiusChange(item))
    },
    actionPanelEditBoxShadow1Change: (item) => {
      dispatch(action.actionPanelEditBoxShadow1Change(item))
    },
    actionPanelEditBoxShadow2Change: (item) => {
      dispatch(action.actionPanelEditBoxShadow2Change(item))
    },
    actionPanelEditBoxShadow3Change: (item) => {
      dispatch(action.actionPanelEditBoxShadow3Change(item))
    },
    actionPanelEditBoxShadowColorChange: (item) => {
      dispatch(action.actionPanelEditBoxShadowColorChange(item))
    },
    actionPanelEditZIndexChange: (item) => {
      dispatch(action.actionPanelEditZIndexChange(item))
    },
    actionEditWidthDecrease: () => {
      dispatch(action.actionEditWidthDecrease())
    },
    actionEditWidthIncrease: () => {
      dispatch(action.actionEditWidthIncrease())
    },
    actionEditHeightDecrease: () => {
      dispatch(action.actionEditHeightDecrease())
    },
    actionEditHeightIncrease: () => {
      dispatch(action.actionEditHeightIncrease())
    },
    actionEditMoveLeftDecrease: (i) => {
      dispatch(action.actionEditMoveLeftDecrease(i))
    },
    actionEditMoveLeftIncrease: (i) => {
      dispatch(action.actionEditMoveLeftIncrease(i))
    },
    actionEditMoveTopDecrease: (i) => {
      dispatch(action.actionEditMoveTopDecrease(i))
    },
    actionEditMoveTopIncrease: (i) => {
      dispatch(action.actionEditMoveTopIncrease(i))
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(PanelFormEdit)