import React, { Component } from 'react'
import { connect } from 'react-redux';
import {actionTogglePanel} from '../../../redux/actions/panelAction'

class Panel extends Component {
    handlePanelClick=()=>{
        this.props.actionTogglePanel({
            id:this.props.i,
            width: parseInt(this.props.width),
            height: parseInt(this.props.height),
            left: parseInt(this.props.left),
            top: parseInt(this.props.top),
            margin: parseInt(this.props.margin),
            backgroundColor: this.props.backgroundColor,
            padding: parseInt(this.props.padding),
            borderRadius: parseInt(this.props.borderRadius),
            boxShadow1:  parseInt(this.props.boxShadow1),
            boxShadow2:  parseInt(this.props.boxShadow2),
            boxShadow3:  parseInt(this.props.boxShadow3),
            boxShadowColor:  this.props.boxShadowColor,
            zIndex: 0,
        })
    }
    render() {
    var tempboxShadow=this.props.boxShadow1 + 'px ' + this.props.boxShadow2 + 'px ' + this.props.boxShadow3 +'px '+ this.props.boxShadowColor
    const style = {
            position: 'absolute',
            width: parseInt(this.props.width),
            height: parseInt(this.props.height),
            left: parseInt(this.props.left),
            top: parseInt(this.props.top),
            margin: parseInt(this.props.margin),
            backgroundColor: this.props.backgroundColor,
            padding: parseInt(this.props.padding),
            borderRadius: parseInt(this.props.borderRadius),
            alignContent: 'center',
            boxShadow:  tempboxShadow,
            zIndex: 0,
        }
        return (
            <div onClick={this.handlePanelClick} style={style}></div>
        )
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionTogglePanel: (item) => {
        dispatch(actionTogglePanel(item))
      },
    }
}
export default connect(null,mapDispatchToProps)(Panel);