import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Col } from 'shards-react';
import * as action from '../../../redux/actions/panelAction'
class PanelFormAdd extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.actionAddPanel();
  };
  handleChangeWidth = (e) => {
    e.preventDefault();
    this.props.actionPanelAddWidthChange(e.target.value);
  };
  handleChangeHeight = (e) => {
    e.preventDefault();
    this.props.actionPanelAddHeightChange(e.target.value);
  };
  handleChangeLeft = (e) => {
    e.preventDefault();
    this.props.actionPanelAddLeftChange(e.target.value);
  };
  handleChangeTop = (e) => {
    e.preventDefault();
    this.props.actionPanelAddTopChange(e.target.value);
  };
  handleChangeMargin = (e) => {
    e.preventDefault();
    this.props.actionPanelAddMarginChange(e.target.value);
  };
  handleChangePadding = (e) => {
    e.preventDefault();
    this.props.actionPanelAddPaddingChange(e.target.value);
  };
  handleChangeBackgroundColor = (e) => {
    e.preventDefault();
    this.props.actionPanelAddBackgroundColorChange(e.target.value);
  };
  handleChangeBorderRadius = (e) => {
    e.preventDefault();
    this.props.actionPanelAddBorderRadiusChange(e.target.value);
  };
  handleChangeBoxShadow1 = (e) => {
    e.preventDefault();
    this.props.actionPanelAddBoxShadow1Change(e.target.value);
  };
  handleChangeBoxShadow2 = (e) => {
    e.preventDefault();
    this.props.actionPanelAddBoxShadow2Change(e.target.value);
  };
  handleChangeBoxShadow3 = (e) => {
    e.preventDefault();
    this.props.actionPanelAddBoxShadow3Change(e.target.value);
  };
  handleChangeBoxShadowColor = (e) => {
    e.preventDefault();
    this.props.actionPanelAddBoxShadowColorChange(e.target.value);
  };
  handleChangeZIndex = (e) => {
    e.preventDefault();
    this.props.actionPanelAddZIndexChange(e.target.value);
  };

  handleKeyUp = (event) => {
    if (event.keyCode === 13) {
      this.props.actionAddPanel();
    }
  }
  handleKeyPress = (event) => {
    var offsetShift=0,offsetCtrl=0,offestDefault=0;
    if (event.shiftKey ) {//shift
      offsetShift=10;offestDefault=0;
    }else {offsetShift=0;}
    if (event.ctrlKey) {//control
      offsetCtrl=5;offestDefault=0;
    }else{offsetCtrl=0;}
    if (!event.shiftKey && !event.ctrlKey){offestDefault=1}
    if (event.keyCode === 37 && (!event.shiftKey || !event.ctrlKey)) {//Trai
      this.props.actionMoveLeftDecrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 38 && (!event.shiftKey || !event.ctrlKey)) {//Len
      this.props.actionMoveTopDecrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 39 && (!event.shiftKey || !event.ctrlKey)) {//Phai
      this.props.actionMoveLeftIncrease(offsetShift+offsetCtrl+offestDefault)
    }
    if (event.keyCode === 40 && (!event.shiftKey || !event.ctrlKey)) {//Xuong
      this.props.actionMoveTopIncrease(offsetShift+offsetCtrl+offestDefault)
    }

    if (event.keyCode === 37 && (event.shiftKey && event.ctrlKey)) {//Trai
      this.props.actionWidthDecrease()
    }
    if (event.keyCode === 38 && (event.shiftKey && event.ctrlKey)) {//Len
      this.props.actionHeightDecrease()
    }
    if (event.keyCode === 39 && (event.shiftKey && event.ctrlKey)) {//Phai
      this.props.actionWidthIncrease()
    }
    if (event.keyCode === 40 && (event.shiftKey && event.ctrlKey)) {//Xuong
      this.props.actionHeightIncrease()
    }
  }
  render() {
    return (
      <Col sm="12" md="4" lg="3">
        <form autoComplete="off" key='AddLineHorizontal' id="login-form" className="form" onSubmit={this.handleSubmit} onKeyDown={this.handleKeyPress} style={{ height: '100vh', overflow: 'auto' }}>
          <h3 className="text-center text-info">ADD PANEL</h3>

          <div className="form-group" style={{ width: '100%' }}>
            <input style={{ width: '100%' }} type="submit" name="submit" className="btn btn-info btn-md" value="Add Data" />
          </div>
          
          <div className="form-group">
            <small className="form-text text-muted">Left:</small>
            <input type="text" onChange={this.handleChangeLeft} placeholder="Left *" id="left" className="form-control" value={this.props.left} />
         
            <small className="form-text text-muted">Top:</small>
            <input type="text" onChange={this.handleChangeTop} placeholder="Top *" id="top" className="form-control" value={this.props.top} />

            <small className="form-text text-muted">Width:</small>
            <input type="text" onChange={this.handleChangeWidth} placeholder="Width *" id="width" className="form-control" value={this.props.width} />

            <small className="form-text text-muted">Height:</small>
            <input type="text" onChange={this.handleChangeHeight} placeholder="Height *" id="height" className="form-control" value={this.props.height} />

            <small className="form-text text-muted">Background Color:</small>
            <input type="color" style={{width:'100%'}} onChange={this.handleChangeBackgroundColor} value={this.props.backgroundColor} />
            <input type="text" onChange={this.handleChangeBackgroundColor} placeholder="Background Color *" className="form-control" value={this.props.backgroundColor} />

            <small className="form-text text-muted">Border Radius:</small>
            <input type="text" onChange={this.handleChangeBorderRadius} placeholder="Border Radius *" className="form-control" value={this.props.borderRadius} />

            <small className="form-text text-muted">Box Shadow 1:</small>
            <input type="text" onChange={this.handleChangeBoxShadow1} placeholder="BoxShadow1 *" className="form-control" value={this.props.boxShadow1} />

            <small className="form-text text-muted">Box Shadow 2:</small>
            <input type="text" onChange={this.handleChangeBoxShadow2} placeholder="BoxShadow2 *" className="form-control" value={this.props.boxShadow2} />

            <small className="form-text text-muted">Box Shadow 3:</small>
            <input type="text" onChange={this.handleChangeBoxShadow3} placeholder="BoxShadow3 *" className="form-control" value={this.props.boxShadow3} />

            <small className="form-text text-muted">Box Shadow Color:</small>
            <input type="color" style={{width:'100%'}} onChange={this.handleChangeBoxShadowColor} value={this.props.boxShadowColor} />
            <input type="text" onChange={this.handleChangeBoxShadowColor} placeholder="BoxShadowColor *" className="form-control" value={this.props.boxShadowColor} />

            <small className="form-text text-muted">Margin:</small>
            <input type="text" onChange={this.handleChangeMargin} placeholder="Margin *" className="form-control" value={this.props.margin} />

            <small className="form-text text-muted">Padding:</small>
            <input type="text" onChange={this.handleChangePadding} placeholder="Padding *" className="form-control" value={this.props.padding} />
          </div>
        </form>
      </Col>
    )
  }
}
const mapStateToProps = (state, ownProps) => {
  return {
    width: state.panel.addPanel.width,
    height: state.panel.addPanel.height,
    left: state.panel.addPanel.left,
    top: state.panel.addPanel.top,
    margin: state.panel.addPanel.margin,
    backgroundColor: state.panel.addPanel.backgroundColor,
    padding: state.panel.addPanel.padding,
    borderRadius: state.panel.addPanel.borderRadius,
    boxShadow1: state.panel.addPanel.boxShadow1,
    boxShadow2: state.panel.addPanel.boxShadow2,
    boxShadow3: state.panel.addPanel.boxShadow3,
    boxShadowColor: state.panel.addPanel.boxShadowColor,
    zIndex: state.panel.addPanel.zIndex,
  }
}
const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    actionAddPanel: () => {
      dispatch(action.actionAddPanel())
    },
    actionPanelAddLeftChange: (item) => {
      dispatch(action.actionPanelAddLeftChange(item))
    },
    actionPanelAddTopChange: (item) => {
      dispatch(action.actionPanelAddTopChange(item))
    },
    actionPanelAddWidthChange: (item) => {
      dispatch(action.actionPanelAddWidthChange(item))
    },
    actionPanelAddHeightChange: (item) => {
      dispatch(action.actionPanelAddHeightChange(item))
    },
    actionPanelAddMarginChange: (item) => {
      dispatch(action.actionPanelAddMarginChange(item))
    },
    actionPanelAddBackgroundColorChange: (item) => {
      dispatch(action.actionPanelAddBackgroundColorChange(item))
    },
    actionPanelAddPaddingChange: (item) => {
      dispatch(action.actionPanelAddPaddingChange(item))
    },
    actionPanelAddBorderRadiusChange: (item) => {
      dispatch(action.actionPanelAddBorderRadiusChange(item))
    },
    actionPanelAddBoxShadow1Change: (item) => {
      dispatch(action.actionPanelAddBoxShadow1Change(item))
    },
    actionPanelAddBoxShadow2Change: (item) => {
      dispatch(action.actionPanelAddBoxShadow2Change(item))
    },
    actionPanelAddBoxShadow3Change: (item) => {
      dispatch(action.actionPanelAddBoxShadow3Change(item))
    },
    actionPanelAddBoxShadowColorChange: (item) => {
      dispatch(action.actionPanelAddBoxShadowColorChange(item))
    },
    actionPanelAddZIndexChange: (item) => {
      dispatch(action.actionPanelAddZIndexChange(item))
    },
    actionWidthDecrease: () => {
      dispatch(action.actionWidthDecrease())
    },
    actionWidthIncrease: () => {
      dispatch(action.actionWidthIncrease())
    },
    actionHeightDecrease: () => {
      dispatch(action.actionHeightDecrease())
    },
    actionHeightIncrease: () => {
      dispatch(action.actionHeightIncrease())
    },
    actionMoveLeftDecrease: (i) => {
      dispatch(action.actionMoveLeftDecrease(i))
    },
    actionMoveLeftIncrease: (i) => {
      dispatch(action.actionMoveLeftIncrease(i))
    },
    actionMoveTopDecrease: (i) => {
      dispatch(action.actionMoveTopDecrease(i))
    },
    actionMoveTopIncrease: (i) => {
      dispatch(action.actionMoveTopIncrease(i))
    },
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(PanelFormAdd)